.Doc01 {
  padding-top: 0;
  background-color: var(--clr-1);
}
.Doc01 > .container {
  opacity: 0;

  transform: translateY(50px); /* Start slightly below its final position */
  transition: opacity 1s ease-out, transform 1s ease-out;
}
.Doc01 > .container.is-visible {
  opacity: 1;
  transform: translateY(0); /* Move to final position */
}

.Doc01-box {
  width: 100%;

  display: grid;
  grid-template-columns: repeat(3, 33.33%);
  gap: 0;
  justify-content: start;
  align-items: stretch;

  border: solid 1px var(--clr-shadow);
}

.Doc01-box > div {
  width: 100%;
  padding: 3rem;

  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  justify-content: start;
  align-items: start;

  background-color: var(--clr-bg);
}
.Doc01-box > div:nth-child(2) {
  color: var(--clr-2);
  background-color: var(--clr-3);
  background: linear-gradient(180deg, var(--clr-3), var(--clr-4));
}

.Doc01-box > div h4 {
  position: relative;
  z-index: 1;

  margin-bottom: 1rem;
  margin-left: 0.5rem;
  color: var(--clr-2);
}
.Doc01-box > div h4::before {
  content: "";
  position: absolute;
  z-index: -1;
  top: -0.1rem;
  left: -0.75rem;

  width: 2.1rem;
  height: 2.1rem;
  background-color: var(--clr-3);
  border-radius: 50%;
}

.Doc01-box > div:nth-child(2) h4::before {
  background-color: var(--clr-1);
}

/*-- responsive --*/

@media screen and (max-width: 1800px) {
  .Doc01-box > div {
    padding: 2rem;
  }
}

@media screen and (max-width: 768px) {
  .Doc01-box {
    grid-template-columns: 100%;
  }
  .Doc01-box > div {
    padding: 2rem 1rem;
  }
}
