.Hero01 {
  position: relative;
  overflow: hidden;

  height: 100vh;
  height: 100svh;
  padding-bottom: 0;

  justify-content: end;
  align-items: end;
}
.Hero01::before,
.Hero01::after {
  content: "";
  position: absolute;
  z-index: -1;
  bottom: -200px;

  width: 400px;
  height: 400px;

  border-radius: 50%;
  filter: blur(200px);
}
.Hero01::before {
  left: -200px;
  background-color: var(--clr-3);
}
.Hero01::after {
  right: -200px;
  background-color: blue;
}

.Hero01 p {
  font-size: 1.5rem;
}

.Hero01-box {
  width: 100%;

  display: flex;
  flex-direction: row;
  gap: 5rem;
  justify-content: space-between;
  align-items: end;
}

.Hero01-box > img {
  height: 700px;
}

.Hero01-box > div {
  width: 100%;
  padding-bottom: 5rem;

  display: flex;
  flex-direction: column;
  gap: 1rem;
  justify-content: start;
  align-items: start;
}

/*-- responsive --*/
@media screen and (max-width: 1800px) {
  .Hero01-box {
    gap: 5rem;
  }
  .Hero01-box > img {
    width: unset;
    height: 80vh;
    height: 80svh;
  }
}

@media screen and (max-width: 768px) {
  .Hero01 {
    padding-top: 10rem;
  }
  .Hero01::before {
    display: none;
  }
  .Hero01::after {
    content: "";
    position: absolute;
    z-index: 0;
    bottom: -40vw;
    right: -40vw;

    width: 80vw;
    height: 80vw;
    background-color: var(--clr-3);
    border-radius: 50%;
    filter: blur(40vw);
  }

  .Hero01 h5 {
    margin-bottom: 0;
  }
  .Hero01 p {
    font-size: 1rem;
  }

  .Hero01 .container {
    justify-content: start;
    align-items: start;
  }
  .Hero01-box {
    width: 80%;

    display: flex;
    flex-direction: column;
    gap: 2rem;
    justify-content: start;
    align-items: end;
  }

  .Hero01-box > img {
    position: absolute;
    z-index: 1;
    bottom: 0;
    right: 0;

    width: unset;
    height: 80vh;
    height: 80svh;
  }

  .Hero01-box > div {
    position: relative;
    z-index: 2;

    padding-bottom: 2rem;
  }
}
