.BgVid01 {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  height: 100svh;

  background-color: var(--clr-1);
  pointer-events: none;
}
.BgVid01::before {
  content: "";
  position: absolute;
  z-index: 1;
  bottom: 0;
  left: 0;

  width: 100%;
  height: 20rem;

  background: linear-gradient(180deg, transparent, var(--clr-1));
}
.BgVid01 > video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0.1;
}

