.Banner01 {
  padding-top: 0;
  background-color: var(--clr-bg);
}

.Banner01 > .container {
  opacity: 0;

  transform: translateY(50px); /* Start slightly below its final position */
  transition: opacity 1s ease-out, transform 1s ease-out;
}
.Banner01 > .container.is-visible {
  opacity: 1;
  transform: translateY(0); /* Move to final position */
}

.Banner01-box {
  width: 100%;

  display: grid;
  grid-template-columns: repeat(2, calc(50% - 10rem));
  gap: 20rem;
  justify-content: center;
  align-items: center;
}

.Banner01-box > div:nth-child(1) {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  justify-content: start;
  align-items: start;
}
.Banner01-box > div:nth-child(1) a {
  margin-top: 2rem;
}

.Banner01-box > img {
  width: 100%;
  aspect-ratio: 3/2;
  object-fit: cover;
  justify-self: center;

  background-color: var(--clr-3);
}

/*-- responsive --*/

@media screen and (max-width: 1800px) {
  .Banner01-box {
    grid-template-columns: repeat(2, calc(50% - 2.5rem));
    gap: 5rem;
  }
}

@media screen and (max-width: 768px) {
  .Banner01-box {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    justify-content: start;
    align-items: start;
  }
}
