.BgVid02 {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  height: 100%;

  background-color: var(--clr-1);
  pointer-events: none;
}
.BgVid02 > video {
  width: 100%;
  height: 100%;
  object-fit: cover;

  opacity: 0.2;
}

