.Stats01 {
  padding: 5rem;
  background-color: var(--clr-1);
}

.Stats01-box {
  width: 100%;

  display: flex;
  flex-direction: row;
  gap: 0;
  justify-content: space-between;
  align-items: stretch;

  background-color: var(--clr-bg);
  border: solid 1px var(--clr-shadow);
}

.Stats01-box > div {
  width: 100%;
  padding: 3rem;

  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  justify-content: center;
  align-items: center;
}

.Stats01-box > div:nth-child(1) {
  border-right: solid 1px var(--clr-shadow);
}
.Stats01-box > div:nth-child(3) {
  color: var(--clr-2);
  background-color: var(--clr-3);
  background: linear-gradient(180deg, var(--clr-3), var(--clr-4));
}
.Stats01-box > div:nth-child(3) p {
  color: var(--clr-2);
}

/*-- responsive --*/

@media screen and (max-width: 1800px) {
  .Stats01 {
    padding: 2rem;
  }
}

@media screen and (max-width: 768px) {
  .Stats01 {
    padding: 0;
  }

  .Stats01-box {
    width: 100%;

    display: flex;
    flex-direction: column;
    gap: 0;
    justify-content: start;
    align-items: start;

    border: none;
    border-top: solid 1px var(--clr-shadow);
    border-bottom: solid 1px var(--clr-shadow);
  }

  .Stats01-box > div {
    padding: 2rem;
  }

  .Stats01-box > div:nth-child(1) {
    border-right: none;
    border-bottom: solid 1px var(--clr-shadow);
  }
}
