.Join01 {
  padding-top: 0;
  padding-bottom: 2rem;
}

.Join01 p,
.Join01 h5 {
  color: var(--clr-2);
}

.Join01 h5::before {
  background-color: var(--clr-2);
}

.Join01 > .container {
}

.Join01-box {
  position: relative;
  width: 80%;
  padding: 5rem 10rem;

  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  justify-content: center;
  align-items: center;
  text-align: center;

  background-color: transparent;

  overflow: hidden;
}

.Join01-box > div.links {
  margin-top: 1rem;

  display: flex;
  flex-direction: row;
  gap: 1rem;
  justify-content: center;
  align-items: center;
}
.Join01-box > div.links a {
  transition: 300ms;
}
.Join01-box > div.links a:hover {
}
.Join01-box > div.links a img {
  width: 2.2rem;
  aspect-ratio: 1/1;
}

/*-- responsive --*/

@media screen and (max-width: 1800px) {
  .Join01-box {
    width: 100%;
  }
}

@media screen and (max-width: 768px) {
  .Join01 {
    padding: 0;
  }
  .Join01-box {
    width: 100%;
    padding: 2rem 1rem;
  }

  .Join01-box h2 {
    font-size: 10vw;
  }

  .Join01-box > div.links a img {
    width: 2rem;
    aspect-ratio: 1/1;
  }
}
