.Token01 {
}

.Token01-box {
  width: 100%;

  display: grid;
  grid-template-columns: repeat(2, calc(50% - 5rem));
  gap: 10rem;
  justify-content: center;
  align-items: center;
}
.Token01-box > img {
  width: 100%;
  aspect-ratio: 3/2;
  object-fit: cover;
  justify-self: center;

  background-color: var(--clr-3);
}
.Token01-box > div {
  width: 100%;

  display: flex;
  flex-direction: column;
  gap: 2rem;
  justify-content: start;
  align-items: start;
}
.Token01-box > div > div:nth-child(1) {
  width: 100%;

  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  justify-content: start;
  align-items: start;
}
.Token01-box-info {
  width: 100%;

  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  justify-content: start;
  align-items: start;

  border: solid 1px var(--clr-shadow);
}
.Token01-box-info > div {
  width: 100%;
  padding: 0.8rem;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  border-bottom: solid 1px var(--clr-shadow);
}
.Token01-box-info > div:last-child {
  border-bottom: none;
}
.Token01-box-info > div.ca {
  width: 100%;
  padding: 0;

  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;
}
.Token01-box-info > div.ca h4 {
  width: 100%;
  padding: 0.8rem 1rem;

  overflow: hidden;
  white-space: nowrap;
}

.Token01-box-info > div.ca button {
  padding: 0.5rem;

  background-color: var(--clr-bg);
  border: none;
  filter: invert(100%);
}
.Token01-box-info > div.ca button:hover {
  transform: unset;
}
.Token01-box-info > div.ca button:hover img {
  height: 0.9rem;
  margin: 0.75rem 1.15rem;
}
.Token01-box-info > div.ca button img {
  height: 1.2rem;
  margin: 0.6rem 1rem;
}

/*-- responsive --*/

@media screen and (max-width: 1800px) {
  .Token01-box {
    grid-template-columns: repeat(2, calc(50% - 2.5rem));
    gap: 5rem;
  }
}

@media screen and (max-width: 768px) {
  .Token01 {
  }

  .Token01-box {
    width: 100%;

    display: flex;
    flex-direction: column;
    gap: 2rem;
    justify-content: center;
    align-items: center;

    border-radius: 2rem;
  }
}
