.Navbar01 {
  position: absolute;
  z-index: 10;
  top: 0;
  left: 0;

  width: 100%;

  display: flex;
  flex-direction: column;
  gap: 1rem;
  justify-content: center;
  align-items: center;

  background-color: var(--clr-bg);
  border-bottom: solid 1px var(--clr-shadow);

  backdrop-filter: blur(5px);
}
.Navbar01 .container {
  padding: 1rem;
}

.Navbar01-box {
  width: 100%;
  padding: 5px;

  display: flex;
  flex-direction: row;
  gap: 5rem;
  justify-content: space-between;
  align-items: center;
}

.Navbar01-logo {
  display: flex;
  flex-direction: row;
  gap: 0.5rem;
  justify-content: center;
  align-items: center;
}
.Navbar01-logo img {
  width: 1.8rem;
  height: 1.8rem;
}
.Navbar01-logo p {
  font-size: 1.2rem;
  font-weight: 600;
  color: var(--clr-2);
}
.Navbar01-logo p span {
  font-weight: 800;
}

.Navbar01-links {
  display: flex;
  flex-direction: row;
  justify-content: end;
  align-items: center;
  gap: 5px;
}
.Navbar01-links li {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
}
.Navbar01-links li a {
  padding: 5px 10px;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;

  color: var(--clr-2);
  background-color: transparent;
  border: solid 1px var(--clr-shadow);
}
.Navbar01-links li a.blue {
  color: var(--clr-2);
  background-color: blue;
}
.Navbar01-links li a.red {
  color: var(--clr-2);
  background-color: var(--clr-3);
}
.Navbar01-links li a:hover {
  color: var(--clr-2);
  background-color: var(--clr-shadow);
}
.Navbar01-links li a.btn01 {
  color: var(--clr-2);
  background-color: var(--clr-3);
}
.Navbar01-links li a.btn01:hover {
  color: var(--clr-2);
  background-color: var(--clr-1);
}
.Navbar01-links li a.btn01 img {
  height: 0.8rem;
  filter: invert(100%);
}
.Navbar01-links li a.btn01:hover img {
  filter: invert(0%);
}

.Navbar01-links li .links {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
}
.Navbar01-links li .links a img {
  height: 1.05rem;
}

.menu-button {
  position: relative;
  display: none;

  width: 1.5rem;
  height: 1.5rem;
  background-color: transparent;
  border: none;
  border-radius: 0;

  background-image: url(../img/icons/menu.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}

.Navbar01-mobile {
  position: absolute;
  z-index: 10;
  top: 4.4rem;
  left: 0;

  width: 100%;
  padding: 1rem;

  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: start;
  align-items: end;
  gap: 5px;

  background-color: var(--clr-bg);
}

.Navbar01-mobile li {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
}
.Navbar01-mobile li a {
  padding: 10px 20px;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;

  color: var(--clr-2);
  background-color: transparent;
  border: solid 1px var(--clr-shadow);
}
.Navbar01-mobile li a:hover {
  color: var(--clr-2);
  background-color: var(--clr-shadow);
}
.Navbar01-mobile li a.btn01 {
  color: var(--clr-2);
  background-color: var(--clr-3);
}
.Navbar01-mobile li a.btn01:hover {
  color: var(--clr-2);
  background-color: var(--clr-1);
}
.Navbar01-mobile li a.btn01 img {
  height: 0.8rem;
  filter: invert(100%);
}
.Navbar01-mobile li a.btn01:hover img {
  filter: invert(0%);
}

.Navbar01-mobile li .links {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
}
.Navbar01-mobile li .links a {
  padding: 10px;
}
.Navbar01-mobile li .links a img {
  height: 1.05rem;
}

/*-- responsive --*/

@media screen and (max-width: 1800px) {
  .Navbar01-box {
    gap: 5rem;
  }
}
@media screen and (max-width: 768px) {
  .Navbar01-box {
    gap: 2rem;
  }
  .Navbar01-links > li {
    display: none;
  }
  .menu-button {
    display: block;
  }
}
