.Slogan01 {
  padding: 5rem;
  background-color: var(--clr-3);
  background: linear-gradient(180deg, var(--clr-3), var(--clr-4));
}

.Slogan01-box {
  width: 100%;

  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  justify-content: center;
  align-items: center;
  text-align: center;

  text-wrap: balance;
}

/*-- responsive --*/

@media screen and (max-width: 1800px) {
}

@media screen and (max-width: 768px) {
  .Slogan01 {
    padding: 2rem 1rem;
  }
}
